<template>
  <v-card flat width="100%">
    
    <v-card-text class="text-center">
      <v-divider></v-divider>
      <h2 class="my-5">{{ label }}</h2>
      <v-divider></v-divider>
      <!-- {{ value }} -->
      <v-data-table
        :headers="headers"
        :items="data"
        hide-default-footer
      >
        <template v-slot:item.read="{ item }" >
          <v-switch
            @change="handelReadRight($event, item)"
            v-model="item.read"
          ></v-switch>
 
        </template>
        <template v-slot:item.write="{ item }">
      
           <v-switch
         
            v-model="item.write"
          ></v-switch>

        </template>
        <template v-slot:item.update="{ item }">
           <v-switch
            v-model="item.update"
          ></v-switch>

        </template>
        <template v-slot:item.delete="{item }">
           <v-switch
            v-model="item.delete"
          ></v-switch>

        </template>



      </v-data-table>>
 
    </v-card-text>
  </v-card>
</template>

<script>
  export default {
    name: 'Rights',
    props: {
      label: String,
      value: {
        type: Array,
        default: () => []
      }
    },
    data () {
      return { 
        headers: [
         { text: 'Section', value: 'section', width: "50%" },
         { text: 'Read', value: 'read' },
         { text: 'Write', value: 'write' },
         { text: 'Update', value: 'update' },
         { text: 'Delete', value: 'delete' },
        ],
        data: [
          { 
            section: 'Sales',
            value: 'sale',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
           { 
            section: 'Retention',
            value: 'retention',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
           { 
            section: 'Payment Methods',
            value: 'payment_method',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
          { 
            section: 'Affiliate',
            value: 'affiliate',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
           { 
            section: 'Users',
            value: 'user',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
          { 
            section: 'Roles',
            value: 'role',
            read: true,
            write: true,
            update: true,
            delete: true,
          },
          { 
            section: 'Menu',
            value: 'menu',
            read: true,
            write: true,
            update: true,
            delete: true,
          }
        ]
      }
    },
    methods: {
      handelReadRight (evt, item) {
        if (!evt) {
          item.write = false
          item.update = false
          item.delete = false
        }
      }
    },
    watch: {
      data: { 
        deep: true,
        // We have to move our method to a handler field
        handler(v) {
       
          this.$emit('input', v)
        }
      }
    },
    mounted () {
      if (this.value.length < 1) {
        this.$emit('input', this.data)
      }
      console.log(this.value);
      
      this.value.forEach(e => {
        const temp = this.data.filter(el => el.value == e.value )
        if (temp[0]) {
          Object.assign(temp[0], e)
        }
      }) 
    }
  }
</script>
