export default {
    title: 'Payment Method',
    icon: 'mdi-credit-card-settings-outline',
    table: [
        {
            text: 'Name',
            align: 'start',
            sortable: false,
            value: 'name',
        },
        { text: 'Type', value: 'type', width: '20%' },
        { text: 'Active', value: 'active', width: '10%' },
        { text: 'Actions', value: 'actions', width: '100' }
    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Type",
                        rules: [
                            v => !!v || 'Type is required',
                        ],
                        items: [
                            {
                                label: "Credit Card",
                                value: "cc"
                            },
                            {
                                label: "Wire",
                                value: "wire"
                            }
                        ],
                        itemText: "label",
                        itemValue: "value",
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}