export default {
    title: 'Notefication',
    icon: 'mdi-message-alert',
    table: [
        {
            text: 'Type',
            align: 'start',
            sortable: false,
            value: 'type',
        },
        { text: 'Message', value: 'message' },
        { text: 'To', value: 'target.displayName' },
        { text: 'From', value: 'by.displayName' },
        { text: 'Read', value: 'read' },
        { text: 'Actions', value: 'actions', width: '100' }

    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Type",
                        prependIcon: 'mdi-label',
                        required: true,

                        rules: [
                            v => !!v || 'Type Name is required',
                        ],
                        items: ['sale_assign']
                    },

                },
                {
                    input: 'text',
                    name: 'message',
                    bind: {
                        label: "Message",
                        prependIcon: 'mdi-message-text',
                        required: true,
                        rules: [
                            v => !!v || 'Message is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'target',
                    bind: {
                        label: "To",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        rules: [
                            v => !!v || 'Payment Method is required',
                        ],
                    }

                },
                {
                    input: 'hidden',
                    name: 'by',
                    default: JSON.parse(localStorage.getItem('user'))._id
                }
            ]
        }
    ]
}