
import countries from '../country'

export default  {
        title: 'Retention',
        icon: 'mdi-printer-pos',
    filters: [
        {

            input: 'text',
            name: 'tpc',

            bind: {
                dense: true,
                label: 'TP Account',
                prependIcon: 'mdi-account-box'

            }
        },
        // {
        //     input: 'select',
        //     name: 'business_unit',
        //     bind: {
        //         label: "Business Unit",
        //         prependInnerIcon: "mdi-domain",
        //         required: true,
        //         dense: true,
        //         clearable: true,
        //         items: [
        //             {
        //                 label: "English",
        //                 value: "en"
        //             },
        //             {
        //                 label: "Arabic",
        //                 value: "ar"
        //             },
        //             {
        //                 label: "Label 1",
        //                 value: "label1"
        //             }
        //         ],
        //         itemText: "label",
        //         itemValue: "value",
        //     }
        // },
        // {
        //     input: 'range',
        //     name: 'created_on',

        //     bind: {
        //         mode: 'range',
        //         popover: { placement: 'left', visibility: 'click' },
        //         dense: true,
        //         label: "Created Date",
        //         prependIcon: 'mdi-calendar',
        //     }
        // },
    ],
        table: [
            {
              text: 'TP Account',
              align: 'start',
              sortable: false,
              value: 'tpc',
            },
            { text: 'Account name', value: 'name' },
            { text: 'Amount', value: 'amount' },
            { text: 'Affiliate', value: 'affiliate.name' },
            // { text: 'Email', value: 'email' },
            { text: 'Payment Method', value: 'payment_method.name'},
            { text: 'Created on', value: 'created_on' },
            { text: 'Created By', value: 'user.displayName' },
            { text: 'Business Unit', value: 'business_unit' },

            { text: 'Actions', value: 'actions', width: '100' } 
           
        ],
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'text',
                        name: 'name',
                        bind: {
                            label: "Account Name",
                            prependIcon: 'mdi-account-box',
                            required: true,
                            rules: [
                                v => !!v || 'ID is required',
                            ],
                        } 
                    },
                    {
                        input: 'text',
                        name: 'tpc',
                        bind: {
                            label: "ID",
                            prependIcon: 'mdi-card-account-details',
                            required: true,
                            rules: [
                                v => !!v || 'ID is required',
                            ],
                        } 
                    },
                    {
                        input: 'text',
                        name: 'amount',
                        bind: {
                            label: "Amount",
                            prependIcon: 'mdi-cash',
                            required: true,
                            rules: [
                                v => !!v || 'Amount is required',
                            ],
                        } 
                    },
                    // {
                    //     input: 'text',
                    //     name: 'email',
                    //     bind: {
                    //         prependIcon: 'mdi-email',
                    //         label: "Email", 
                    //         required: true,
                    //         rules: [
                    //             v => !!v || 'Email is required',
                    //             v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid',
                    //         ],
                    //     } 
                    // },
                    {
                        input: 'autocomplete',
                        name: 'payment_method',
                        bind: {
                            label: "Payment Method",
                            prependIcon: "mdi-credit-card-settings-outline",
                            collection: 'payment_method',
                            itemText: "name",
                            itemValue: "_id", 
                            required: true,
                            rules: [
                                v => !!v || 'Payment Method is required',
                            ],
                        } 
                    },
                    {
                        input: 'hidden',
                        name: 'user',
                        // default: JSON.parse(localStorage.getItem('user'))._id,
                        default: state => state.auth.user._id

                        
                    },
                    {
                        input: 'date',
                        name: 'created_on',
                        default: (new Date()).toISOString().split('T')[0],
                        bind: {
                            label: "Created Date",
                            prependIcon: 'mdi-calendar', 
                            required: true,
                            rules: [
                                v => !!v || 'Created On is required',
                            ],
                        } 
                    },
                    {
                        input: 'hidden',
                        name: 'business_unit',
                        default: state => state.auth.user.business_unit

                    },
                    {
                        input: 'autocomplete',
                        name: 'country',
                        bind: {
                            label: "Country",
                            prependIcon: "mdi-flag",
                            items: countries, 
                            itemText: "name",
                            itemValue: "code",
                            hideAddNew: true,
                            required: true,
                            rules: [
                                v => !!v || 'Country is required',
                            ],
                        } 
                    },
                    {
                        input: 'autocomplete',
                        name: 'affiliate',
                        bind: {
                            dense: true,
                            label: "Affiliate",
                            collection: 'affiliate',
                            itemText: "name",
                            itemValue: "_id",
                            prependIcon: "mdi-account-network-outline",

                        }
                    }
                ]
            }
        ]
}