export default {
    title: 'Affiliate',
    icon: 'mdi-account-network-outline',
    table: [
        {
            text: 'Name',
            align: 'start',
            sortable: false,
            value: 'name',
        },
        { text: 'Active', value: 'active', width: '10%' },
        { text: 'Actions', value: 'actions', width: '100' }
    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}