export default {
        title: 'Roles',
        icon: 'mdi-key',
        table: [
            {
                text: 'Display Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            { text: 'Role', value: 'role' },
            { text: 'Menu', value: 'menu.name' },
            { text: 'Actions', value: 'actions', width: '100' } 
            
        ],
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'text',
                        name: 'name',
                        bind: {
                            label: "Role Name",
                            prependIcon: 'mdi-label',
                            required: true,
                            
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        },
                        on: {
                            input(v) {
                                const role = v.replace(/\s+/g, '_').toLowerCase()
                                window.$form.data.role = role 
                               
                            }
                        }
                    },
                    {
                        input: 'text',
                        name: 'role',
                        bind: {
                            label: "Role Alias",
                            disabled:true,
                            autocomplete: 'username',
                            prependIcon: 'mdi-key',
                            required: true,
                            rules: [
                                v => !!v || 'Username is required',
                            ],
                        }
                    },
                    {
                        input: 'autocomplete',
                        name: 'menu',
                        bind: {
                            label: "Menu",
                            prependIcon: "mdi-menu",
                            collection: 'menu',
                            itemText: "name",
                            itemValue: "_id",
                            // required: true,


                            // rules: [
                            //     v => !!v || 'Menu is required',
                            // ],
                        }
                    },
                    {
                        input: 'rights',
                        name: 'rights',
                        default: [],
                        bind: {
                            label: "Permissions",
                        }
                    } 
                ]
            } 
        ]
    }