export default {
    title: 'Menu',
    icon: 'mdi-microsoft-xbox-controller-menu',
    table: [
        {
            text: 'Name',
            align: 'start',
            sortable: false,
            value: 'name',
        },
        { text: 'Actions', value: 'actions', width: '100' }
    ],
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'menu_item',
                    name: 'menu_item',
                    default: [
                        { "name": "Dashboard", "icon": "mdi-view-dashboard", "path": "/" },
                        { "name": "Sales", "icon": "mdi-printer-pos", "path": "/sale" },
                        { "name": "Retentions", "icon": "mdi-printer-pos", "path": "/retention" },
                        { "name": "Payment Method", "icon": "mdi mdi-credit-card-settings-outline", "path": "/payment_method" },
                        { "name": "Affiliate", "icon": "mdi-account-network-outline", "path": "/affiliate" },
                        { "name": "Users", "icon": "mdi-account-multiple", "path": "/user" },
                        { "name": "Roles", "icon": "mdi-key", "path": "/role" },
                        { "name": "Menus", "icon": "mdi-menu", "path": "/menu" }],
                    bind: {
                         label: 'Menu Items'
                    }
                }
                
            ]
        }
    ]
}